import React, { Component, Suspense, lazy } from 'react';
import { Link, HashRouter as Router, Route } from "react-router-dom";
import Loder from '../Loder';
import { gojsData } from "../../assets/data";
import "./index.less";

const GoJS00 = lazy(() => import('./Chapters/00'));
const GoJS01 = lazy(() => import('./Chapters/01'));
const GoJS02 = lazy(() => import('./Chapters/02'));
const GoJS03 = lazy(() => import('./Chapters/03'));
const GoJS04 = lazy(() => import('./Chapters/04'));
const GoJS05 = lazy(() => import('./Chapters/05'));
const GoJS06 = lazy(() => import('./Chapters/06'));
const PageFooter = lazy(() => import('../PageFooter'));

export default class index extends Component {
    componentDidMount() {
        window.addEventListener('hashchange', () => {
            document.getElementsByClassName("header")[0].className = "ant-layout-header header header-visible";
        })
    }
    render() {
        return (
            <Router>
                <Suspense fallback={<Loder />}>
                    <Route exact path={gojsData.catalog[0].href} render={() => (<GoJS00 />)} />
                    <Route exact path={gojsData.catalog[1].href} render={() => (<GoJS01 />)} />
                    <Route exact path={gojsData.catalog[2].href} render={() => (<GoJS02 />)} />
                    <Route exact path={gojsData.catalog[3].href} render={() => (<GoJS03 />)} />
                    <Route exact path={gojsData.catalog[4].href} render={() => (<GoJS04 />)} />
                    <Route exact path={gojsData.catalog[5].href} render={() => (<GoJS05 />)} />
                    <Route exact path={gojsData.catalog[6].href} render={() => (<GoJS06 />)} />
                    <Route exact path={gojsData.baseHref} render={() => (
                        <div className="catalog">
                            <div className="container">
                                <div className="title">
                                    <h1>{gojsData.title}</h1>
                                </div>
                                <div className="contents">
                                    <ul>
                                        {gojsData.catalog.map((item, index) => {
                                            return (
                                                <li key={index}><Link to={item.href}>{item.title}</Link></li>
                                            )
                                        })}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    )} />
                    <Route path="/gojs/:id" render={() => (<PageFooter />)} />
                </Suspense>
            </Router>
        )
    }
}
